import React, {useState} from 'react';
import { Line } from 'react-konva';
import {IShelve, ShapeMode} from '../types/__types';
import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;
import {stopPropagation} from '../utils/events';
import ControlCircles from './ControlCircles';

const Shape = (shelve:IShelve) => {
    const [mode, setMode] = useState<number>(ShapeMode.NONE);
    const changeMode = (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        setMode(mode < ShapeMode.RESIZE ? mode + 1 : ShapeMode.NONE);
        stopPropagation(evt);
    };
    if (shelve.points) {
        return <>
            <Line
                key="line"
                points={shelve.points}
                stroke={shelve.color}
                strokeWidth={3}
                dash={[10, 10]}
                closed
                fill={shelve.color}
                onMouseDown={changeMode}
                onTouchStart={changeMode}
            />
            {mode === ShapeMode.POINT && <ControlCircles {...shelve} />}
        </>;
    }
    return null;
};


export default Shape;
