import React from 'react';
import { Label, Text, Tag } from 'react-konva';
import {IButton} from '../types/__types';
import {defaultCursor, pointerCursor, stopPropagation} from '../utils/events';

const CloseButton = (props: IButton) => {
    const { id, x, y, color } = props;
    const handleClick = () => {
        props.handleClick(id);
    };

    const width = 40;
    const height = 40;
    return (
        <Label
            key="label"
            x={x - width / 2}
            y={y - height / 2}
            onClick={handleClick}
            onTouchStart={handleClick}
            onMouseDown={stopPropagation}
            onMouseUp={stopPropagation}
            onMouseMove={stopPropagation}
            onMouseEnter={pointerCursor}
            onMouseLeave={defaultCursor}
            width={width}
            height={height}
            opacity={0.5}
        >
            <Tag
                fill={color}
                lineJoin="round"
                shadowColor="black"
                cornerRadius={width / 2}

            />
            <Text
                text="X"
                fill="white"
                padding={15}
                onClick={handleClick}
                onTap={handleClick}
            />
        </Label>
    );
};


export default CloseButton;
