import {IShelve, ShapeType} from '../types/__types';

export const validatePoints = (_x: number, _y: number, _width: number, _height: number) => {
    let x = _x, y = _y, width = _width, height = _height;
    if (_width < 0) {
        x = _x + width;
        width = -width;
    }
    if (_height < 0) {
        y = _y + height;
        height = -height;
    }
    return [x, y, x + width, y, x + width, y + height, x, y + height];
};

export const shelveX = (shelve: IShelve) => {
    // @ts-ignore
    return shelve.type === ShapeType.RECTANGLE ? shelve.x + shelve.width / 2 : shelve.points[0] + (shelve.points[2] - shelve.points[0]) / 2;
};

export const shelveY = (shelve: IShelve) => {
    // @ts-ignore
    return shelve.type === ShapeType.RECTANGLE ? shelve.y + shelve.height / 2 : shelve.points[1] + (shelve.points[7] - shelve.points[1]) / 2;
};
