import React, {Component, ReactElement} from 'react';
import { Circle } from 'react-konva';
import {IShelve} from '../types/__types';
import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;
import {stopPropagation} from '../utils/events';

class ControlCircles extends Component<IShelve, any>  {
    constructor(props:IShelve) {
        super(props);
        this.state = {
            pressed: false
        };
    }

    handleMouseEvent = (point: number, evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (this.props.points && position) {
            const newPoints = [...this.props.points];
            newPoints[point] = position.x;
            newPoints[point + 1] = position.y;
            this.props.onChange && this.props.onChange(this.props.id, newPoints);
        }
        stopPropagation(evt);
    };
    handleMouseDown = (point: number) => (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        this.setState({pressed: true});
        this.handleMouseEvent(point, evt);
    };

    handleMouseUp = (point: number) => (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        this.setState({pressed: false});
        this.handleMouseEvent(point, evt);
    };

    handleMouseMove = (point: number) => (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        if (this.state.pressed) {
            this.handleMouseEvent(point, evt);
        }
    };

    render() {
        const { points } = this.props;
        const circles:ReactElement[] = [];
        for (let i = 0; i < 8; i = i + 2) {
            points && circles.push(
                <Circle
                    key={i}
                    x={points[i]}
                    y={points[i + 1]}
                    radius={20}
                    fill="green"
                    onMouseDown={this.handleMouseDown(i)}
                    onMouseUp={this.handleMouseUp(i)}
                    onMouseMove={this.handleMouseMove(i)}
                    onTouchStart={this.handleMouseDown(i)}
                    onTouchEnd={this.handleMouseUp(i)}
                    onTouchMove={this.handleMouseMove(i)}
                />
            );
        }
        return circles;
    }
}

export default ControlCircles;
