import React, { useState } from 'react';
import { Stage, Layer } from 'react-konva';
import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;
import {IProps, IShelve, ShapeType} from '../types/__types';
import CloseButton from './CloseButton';
import Rectangle from './Rectangle';
import Shape from './Shape';
import {shelveX, shelveY, validatePoints} from '../utils/geometry';

function randomColor() {
    let color = '#';
    for (let i = 0; i < 6; i++) {
        color += Math.floor(Math.random() * 10);
    }
    return color;
}

const MainComponent = (props:IProps) => {
    const [newShelve, setNewShelve] = useState<IShelve | null>(null);

    const handleMouseDown = (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (!newShelve && position) {
            const { x, y } = position;
            setNewShelve({ id: 0, x, y, width: 0, height: 0, color: 'black', type: ShapeType.RECTANGLE });
        }
    };

    const handleMouseUp = (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (newShelve && position) {
            const { shelves } = props;
            const sx = newShelve.x;
            const sy = newShelve.y;
            if (sx !== undefined && sy !== undefined) {
                const { x, y } = position;
                const shelveToAdd:IShelve = {
                    id: shelves.length + 1,
                    points: validatePoints(sx, sy, x - sx, y - sy),
                    color: randomColor(),
                    type: ShapeType.SHAPE
                };
                setNewShelve(null);
                const newShelves = [...shelves];
                newShelves.push(shelveToAdd);
                props.onChange(newShelves);
            }
        }
    };

    const deleteShelve = (id:number) => {
        const { shelves } = props;
        props.onChange(shelves.filter((shelve:IShelve) => shelve.id !== id));
    };

    const handleMouseMove = (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (newShelve && position) {
            const sx = newShelve.x;
            const sy = newShelve.y;
            if (sx !== undefined && sy !== undefined) {
                const {x, y} = position;
                setNewShelve({
                    id: 0,
                    x: sx,
                    y: sy,
                    width: x - sx,
                    height: y - sy,
                    color: 'black',
                    type: ShapeType.RECTANGLE
                });
            }
        }
    };

    const editShape = (id: number, points: number[]) => {
        const { shelves } = props;
        const newShelves = [...shelves];
        for (let i = 0; i < newShelves.length; i++) {
            const shelve: IShelve = newShelves[i];
            if (shelve.id === id) {
                shelve.points = points;
                props.onChange(newShelves);
                break;
            }
        }
    };

    const shelvesToDraw = [...props.shelves].concat(newShelve ? newShelve : []);
    return (
        <Stage
            className="stage"
            onMouseDown={handleMouseDown}
            onMouseUp={handleMouseUp}
            onMouseMove={handleMouseMove}
            onTouchStart={handleMouseDown}
            onTouchEnd={handleMouseUp}
            onTouchMove={handleMouseDown}
            width={1000}
            height={800}
        >
            <Layer>
                {shelvesToDraw.map((shelve:IShelve) => {
                    return [
                        shelve.type === ShapeType.RECTANGLE
                            ? <Rectangle key={shelve.type} {...shelve} />
                            : <Shape
                                key={shelve.type}
                                {...shelve}
                                onChange={editShape}
                            />,
                        <CloseButton
                            key="close"
                            id={shelve.id}
                            x={shelveX(shelve)}
                            y={shelveY(shelve)}
                            color={shelve.color}
                            handleClick={deleteShelve}
                        />
                    ];
                })}
            </Layer>
        </Stage>
    );
};


export default MainComponent;
