export type IProps = {
    shelves: any[],
    onChange: (shelves: any[]) => void
};

export enum ShapeType {
    RECTANGLE = 'rectangle',
    SHAPE = 'shape'
}

export enum ShapeMode {
    NONE,
    POINT,
    RESIZE
}

export type IShelve = {
    id: number
    x?: number,
    y?: number,
    width?: number,
    height?: number,
    points?: number[],
    color: string,
    type: string,
    onChange?: (id: number, points: number[]) => void
};

export type IButton = {
    id: number,
    x: number,
    y: number,
    color: string,
    handleClick: (key:number) => void
};

export type IBackground = {
    imageUrl: string
};
