import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;

export const stopPropagation = (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
    evt.cancelBubble = true;
};

const changeCursor = (style:string) => {
    return (evt:KonvaEventObject<MouseEvent|TouchEvent>) => {
        const container = evt.target.getStage()?.container();
        if (container) {
            container.style.cursor = style;
        }
    };
};

export const pointerCursor = changeCursor('pointer');
export const defaultCursor = changeCursor('default');
