import React from 'react';
import { Rect } from 'react-konva';
import {IShelve} from '../types/__types';
const Rectangle = (shelve:IShelve) => {
    return (
        <Rect
            x={shelve.x}
            y={shelve.y}
            width={shelve.width}
            height={shelve.height}
            stroke={shelve.color}
            strokeWidth={3}
            dash={[10, 10]}
        />
    );
};


export default Rectangle;
